.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.search {
  text-align: center;
  width: 90%;
}

.search input {
  width: 100%;
  margin: 0;
  height: 51px;
  background: none;
  border-bottom: 2px solid #304dad;
  border-radius: 0;
  border-top: 1px solid transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  padding: 0 5px;
  position: relative;
}

.search input::placeholder {
  text-align: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 85%;
}

.movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  text-decoration: none;
  color: inherit;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .data {
  display: table;
  float: left;
  position: relative;
  width: 415px;
}

.movie h1 {
  margin: 0;
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie h1 a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie p {
  display: flex;
  align-items: center;
  color: #2c2c2c;
  font-size: 13px;
}

.movie p span {
  flex: 0 0 80px;
  font-weight: 700;
}

@media (max-width: 1080px) {
  .movies {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 85%;
  }

  .movie {
    width: 100%;
  }

  .movie img {
    position: relative;
    max-width: 150px;
    width: 100%;
    margin-right: 20px;
  }
}
